  pointer-events: none;
  -webkit-transform-origin: 66% 66%;
  transform-origin: 66% 66%;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  -webkit-transition: all .3s;
  transition: all .3s;
  position: absolute;
  right: -1.2rem;
  top: calc(50% + 2px);
}

.s-header__nav li:hover>a,
.s-header__nav li:focus>a {
  color: black;
}

.s-header__nav li:hover li,
.s-header__nav li:focus li {
  background: transparent;
}

.s-header__nav li.current>a {
  color: black;
}

.s-header__nav li.current>a::before {
  content: "";
  display: block;
  height: 2px;
  width: 100%;
  background-color: black;
  position: absolute;
  left: 0;
  bottom: 0;
}

.s-header__nav li ul {
  z-index: 200;
  font-size: 1.5rem;
  font-weight: 400;
  margin: 0;
  padding: 1.8rem 0;
  background: #f4f4f4;
  border-radius: 0 0 4px 4px;
  -webkit-box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.05);
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.05);
  -webkit-transform: translate3d(0, 15px, 0);
  transform: translate3d(0, 15px, 0);
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all .5s;
  transition: all .5s;
  position: absolute;
  top: 100%;
  left: 0;
}

.s-header__nav li ul ul {
  position: absolute;
  top: 0;
  left: 100%;
  left: calc(100% + 1px);
  border-radius: 0 0 4px 4px;
  padding-top: 1.2rem;
}

.s-header__nav li ul li {
  display: block;
  text-align: left;
  padding: 0;
  margin: 0;
  min-height: 3.2rem;
  width: 100%;
}

.s-header__nav li ul li a {
  display: block;
  white-space: nowrap;
  padding: .7rem 3rem .7rem 2rem;
  font-family: var(--font-1);
  line-height: 1.8rem;
  color: rgba(0, 0, 0, 0.6);
}

.s-header__nav li ul li a:hover,
.s-header__nav li ul li a:focus {
  color: black;
}

.s-header__nav li:hover>ul {
  opacity: 1;
  visibility: visible;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

/* ------------------------------------------------------------------- 
  * ## header-toggle
  * ------------------------------------------------------------------- */
.s-header__toggle-menu {
  display: none;
  width: 48px;
  height: 48px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  position: absolute;
  right: 4rem;
  top: 50%;
}

.s-header__toggle-menu span {
  display: block;
  width: 28px;
  height: 3px;
  margin-top: -1.5px;
  position: absolute;
  right: 10px;
  top: 50%;
  bottom: auto;
  left: auto;
  background-color: black;
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
}

.s-header__toggle-menu span::before,
.s-header__toggle-menu span::after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: inherit;
  left: 0;
}

.s-header__toggle-menu span::before {
  top: -10px;
}

.s-header__toggle-menu span::after {
  bottom: -10px;
}

/* ------------------------------------------------------------------- 
  * ## header-search
  * ------------------------------------------------------------------- */
.s-header__search {
  z-index: 300;
  display: block;
  text-align: center;
  background: white;
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  -webkit-transition: all .3s;
  transition: all .3s;
}

.s-header__search-form {
  width: 100%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  position: absolute;
  top: 50%;
}

.s-header__search-form label {
  color: black;
}

.s-header__search-form::after {
  content: "Pressione Enter para iniciar a sua pesquisa";
  display: block;
  letter-spacing: 0.6px;
  font-size: 1.6rem;
  margin-top: var(--vspace-1);
  text-align: center;
  color: rgba(0, 0, 0, 0.5);
}

.s-header__search-form input[type="search"] {
  background-color: transparent;
  color: black;
  height: auto;
  width: 100%;
  font-family: var(--font-2);
  font-weight: 600;
  font-size: 6rem;
  line-height: 1.5;
  border: none;
  border-bottom: 1px solid var(--color-border) !important;
  max-width: 680px;
  padding-top: .8rem !important;
  padding-bottom: .8rem !important;
  margin: 0 auto;
  text-align: center;
}

.s-header__search-form input[type="search"]::-webkit-search-decoration,
.s-header__search-form input[type="search"]::-webkit-search-cancel-button,
.s-header__search-form input[type="search"]::-webkit-search-results-button,
.s-header__search-form input[type="search"]::-webkit-search-results-decoration {
  -webkit-appearance: none;
}

.s-header__search-form input[type="search"]::-webkit-input-placeholder {
  color: black;
  text-align: center !important;
  opacity: 1;
}

.s-header__search-form input[type="search"]:-moz-placeholder {
  color: black;
  text-align: center !important;
  opacity: 1;
}

.s-header__search-form input[type="search"]::-moz-placeholder {
  color: black;
  text-align: center !important;
  opacity: 1;
}

.s-header__search-form input[type="search"]:-ms-input-placeholder {
  color: black;
  text-align: center !important;
  opacity: 1;
}

.s-header__search-form input[type="search"].placeholder {
  color: black;
  text-align: center !important;
  opacity: 1;
}

.s-header__search-form input[type="search"]:focus {
  outline: none;
}

.s-header__search-form input[type="submit"] {
  display: none;
}

body.search-is-visible {
  overflow: hidden;
}

.search-is-visible .s-header__search {
  opacity: 1;
  visibility: visible;
}

/* -------------------------------------------------------------------
  * ## search triggers
  * ------------------------------------------------------------------- */
.s-header__search-trigger {
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  -ms-flex-align: center;
  -webkit-box-align: center;
  align-items: center;
  -ms-flex-pack: center;
  -webkit-box-pack: center;
  justify-content: center;
  width: 4rem;
  height: 4rem;
