  transition: all .3s;
  position: relative;
}

.ss-go-top a:hover,
.ss-go-top a:focus {
  background-color: black;
}

.ss-go-top a:hover svg path,
.ss-go-top a:focus svg path {
  fill: white;
}

.ss-go-top svg {
  height: 2.4rem;
  width: 2.4rem;
}

.ss-go-top svg path {
  fill: black;
}

.ss-go-top.link-is-visible {
  opacity: 1;
  visibility: visible;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

