.s-content__author .author-social {
  list-style: none;
  margin-left: 0;
  font-size: var(--text-xs);
  text-transform: uppercase;
  letter-spacing: .2em;
}

.s-content__author .author-social li {
  display: inline-block;
  margin-right: 0.8rem;
  padding-left: 0;
}

.s-content__author .author-social a {
  color: var(--color-text-light);
}

/* ------------------------------------------------------------------- 
  * ## single post page nav
  * ------------------------------------------------------------------- */
.s-content__pagenav {
  padding-top: var(--vspace-0_5);
  margin: 12rem 0;
  border-top: 1px solid var(--color-border);
}

.s-content__pagenav div {
  float: left;
  width: 50%;
  padding-top: var(--vspace-0_5);
  padding-right: var(--gutter-lg);
}

.s-content__pagenav a {
  font-family: var(--font-2);
  font-weight: 600;
  font-size: var(--text-lg);
  line-height: var(--vspace-1);
  border: none;
  color: black;
}

.s-content__pagenav a span {
  display: block;
  font-family: var(--font-1);
  font-weight: 400;
  font-size: var(--text-sm);
  line-height: var(--vspace-1);
  margin-bottom: var(--vspace-0_5);
  color: var(--color-text-light);
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.s-content__pagenav a:hover span {
  color: black;
}

/* ------------------------------------------------------------------- 
  * responsive:
  * blog styles and blog components
  * ------------------------------------------------------------------- */
@media screen and (max-width: 800px) {
  .s-content__author {
    padding-left: 0;
    text-align: center;
  }

  .s-content__author img {
    position: static;
    width: 6.6rem;
    height: 6.6rem;
    margin: 0 0 0.8rem 0 !important;
  }
}

@media screen and (max-width: 640px) {
  .s-content__pagenav {
    text-align: center;
  }

  .s-content__pagenav div {
    float: none;
    width: 100%;
    padding-right: 0;
  }

  .s-content__pagenav div:first-child {
    margin-bottom: var(--vspace-0_5);
  }
}


/* -------------------------------------------------------------------
  * ## comments 
  * ------------------------------------------------------------------- */
.comments-wrap {
  margin-top: var(--vspace-3);
  padding: var(--vspace-3_5) 0 var(--vspace-3_5);
  background-color: var(--color-gray-1);
}

.comments-wrap .column {
  padding: 0 calc(8rem + var(--gutter-lg));
}

.comments-wrap h3 {
  margin-top: 0;
  margin-bottom: var(--vspace-0_5);
}

.comments-wrap h3 span {
  display: block;
  font-family: var(--font-1);
  font-weight: 400;
  font-size: calc(var(--text-size) * 0.8889);
  line-height: var(--vspace-0_75);
  color: var(--color-text-light);
  margin-top: calc(0.125 * var(--space));
}

/* comments
  * -------------------------------------------------- */
#comments {
  padding-top: var(--vspace-0_5);
  padding-bottom: var(--vspace-0_5);
}

#comments ol,
#comments ul {
  list-style: none;
}

.commentlist {
  margin: var(--vspace-2) 0 var(--vspace-1_5);
  padding: 0;
}

.commentlist>.comment {
  position: relative;
  list-style: none;
  margin: 0;
  padding: 2rem 0 0 0;
  padding-left: 14%;
}

.comment__avatar {
  position: absolute;
  left: 0;
  display: block;
}

.comment__avatar img {
  height: var(--vspace-2);
  width: var(--vspace-2);
  border-radius: 100%;
  vertical-align: bottom;
}

.comment__info {
  position: relative;
}

.comment__info .comment__author {
  font-weight: 600;
  font-size: var(--text-size);
  line-height: var(--vspace-1);
  font-style: normal;
  color: black;
}

.comment__meta {
  display: block;
  font-style: normal;
  line-height: var(--vspace-0_75);
  color: var(--color-text-light);
  text-align: right;
  width: 150px;
  position: absolute;
  top: 0;
  right: 0;
}

.comment__meta .comment__time {
  font-size: calc(var(--text-size) * 0.7778);
  text-transform: uppercase;
  letter-spacing: .1rem;
  color: var(--color-text-light);
}

.comment__meta .comment__reply a {
  font-size: var(--text-xs);
  font-family: var(--font-1);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: .2rem;
  color: black;
}

.comment__text {
  clear: both;
  margin: var(--vspace-0_5) 0 0 0;
  padding: 0 175px 0 0;
}

.comment ul.children {
  margin: 0;
  padding: 0;
}

.comment ul.children li {
  padding-left: 5%;
  margin-top: calc(var(--vspace-1) + 2rem);
  border-left: 1px solid var(--color-border);
}

/* comments form
  * ---------------------------- */
.comments-wrap .comment-respond {
  margin-top: var(--vspace-2);
}

.comment-respond form {
  padding-top: var(--vspace-1_75);
}

/* ------------------------------------------------------------------- 
  * responsive:
  * comments
  * ------------------------------------------------------------------- */
@media screen and (max-width: 1200px) {
  .comments-wrap .column {
    padding: 0 calc(4.8rem + var(--gutter-md));
  }
}

@media screen and (max-width: 1100px) {
  .comments-wrap .column {
    padding: 0 calc(4rem + var(--gutter-md));
  }
}

@media screen and (max-width: 1020px) {
  .comments-wrap .column {
    padding: 0 calc(3.2rem + var(--gutter-md));
  }
}

@media screen and (max-width: 1000px) {
  .comment__avatar img {
    height: var(--vspace-1_75);
    width: var(--vspace-1_75);
  }

  .comment__meta {
    text-align: left;
    width: auto;
    position: static;
  }

  .comment__meta .comment__time,
  .comment__meta .comment__reply {
    display: inline-block;
  }

  .comment__meta .comment__time {
    margin-right: 12px;
  }

  .comment__text {
    padding: 0;
  }

  .comments-wrap .comment-respond {
    margin-top: var(--vspace-1_5);
  }
}

@media screen and (max-width: 800px) {
  .comments-wrap {
    padding: var(--vspace-2) 0 var(--vspace-1);
  }

  .comments-wrap .column {
    padding: 0 var(--gutter-md);
  }

  .comment__avatar img {
    margin-top: 0;
    height: var(--vspace-1_5);
    width: var(--vspace-1_5);
  }
}

@media screen and (max-width: 600px) {
  .comments-wrap h3 {
    text-align: center;
  }

  .comments-wrap .column {
    padding: 0 var(--gutter-mob);
  }

  .commentlist>.comment {
    padding-top: 0;
    padding-left: 0;
  }

  .comment__avatar {
    display: none;
  }

  .comment ul.children li {
    margin-top: calc(var(--vspace-1) + 0rem);
  }
}

@media screen and (max-width: 500px) {
  .comment ul.children li {
    border-left: none;
  }
}

@media screen and (max-width: 400px) {
  .comments-wrap .column {
    padding: 0;
  }

  .comment-respond form {
    padding-top: var(--vspace-1);
  }
}

.weather-icon {
  position: relative;
  width: 130px;
  height: 130px;
  top: 0;
  float: right;
  margin: 7px 0px 0px 0px;
}

svg {
  height: 500px;
}

.am-weather-cloud-1 {
  -webkit-animation-name: am-weather-cloud-1;
  animation-name: am-weather-cloud-1;
  -webkit-animation-duration: 7s;
  animation-duration: 7s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite
}

.am-weather-cloud-2 {
  -webkit-animation-name: am-weather-cloud-2;
  animation-name: am-weather-cloud-2;
  -webkit-animation-duration: 3s;
  animation-duration: 3s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite
}

.am-weather-sun {
  -webkit-animation-name: am-weather-sun;
  animation-name: am-weather-sun;
  -webkit-animation-duration: 9s;
  animation-duration: 9s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite
}

.am-weather-sun-shiny line {
  -webkit-animation-name: am-weather-sun-shiny;
  animation-name: am-weather-sun-shiny;
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite
}

.am-weather-moon {
  -webkit-animation-name: am-weather-moon;
  animation-name: am-weather-moon;
  -webkit-animation-duration: 6s;
  animation-duration: 6s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-transform-origin: 12.5px 15.15px 0;
  transform-origin: 12.5px 15.15px 0
}

.am-weather-moon-star-1 {
  -webkit-animation-name: am-weather-moon-star-1;
  animation-name: am-weather-moon-star-1;
  -webkit-animation-delay: 3s;
  animation-delay: 3s;
  -webkit-animation-duration: 5s;
  animation-duration: 5s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1
}

.am-weather-moon-star-2 {
  -webkit-animation-name: am-weather-moon-star-2;
  animation-name: am-weather-moon-star-2;
  -webkit-animation-delay: 5s;
  animation-delay: 5s;
  -webkit-animation-duration: 4s;
  animation-duration: 4s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1
}

.am-weather-rain-1 {
  -webkit-animation-name: am-weather-rain;
  animation-name: am-weather-rain;
  -webkit-animation-duration: 8s;
  animation-duration: 8s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite
}

.am-weather-rain-2 {
  -webkit-animation-name: am-weather-rain;
  animation-name: am-weather-rain;
  -webkit-animation-delay: .25s;
  animation-delay: .25s;
  -webkit-animation-duration: 8s;
  animation-duration: 8s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite
}

.am-weather-snow-1 {
  -webkit-animation-name: am-weather-snow;
  animation-name: am-weather-snow;
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite
}

.am-weather-snow-2 {
  -webkit-animation-name: am-weather-snow;
  animation-name: am-weather-snow;
  -webkit-animation-delay: 1.2s;
  animation-delay: 1.2s;
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite
}

.am-weather-snow-3 {
  -webkit-animation-name: am-weather-snow-reverse;
  animation-name: am-weather-snow-reverse;
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite
}

.am-weather-easing-ease-in-out {
  -webkit-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out
}

@-webkit-keyframes am-weather-cloud-1 {
  0% {
    -webkit-transform: translate(-5px, 0);
    transform: translate(-5px, 0)
  }

  50% {
    -webkit-transform: translate(10px, 0);
    transform: translate(10px, 0)
  }

  100% {
    -webkit-transform: translate(-5px, 0);
    transform: translate(-5px, 0)
  }
}

@keyframes am-weather-cloud-1 {
  0% {
    -webkit-transform: translate(-5px, 0);
    transform: translate(-5px, 0)
  }

  50% {
    -webkit-transform: translate(10px, 0);
    transform: translate(10px, 0)
  }

  100% {
    -webkit-transform: translate(-5px, 0);
    transform: translate(-5px, 0)
  }
}

@-webkit-keyframes am-weather-cloud-2 {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0)
  }

  50% {
    -webkit-transform: translate(2px, 0);
    transform: translate(2px, 0)
  }

  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0)
  }
}

@keyframes am-weather-cloud-2 {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0)
  }

  50% {
    -webkit-transform: translate(2px, 0);
    transform: translate(2px, 0)
  }

  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0)
  }
}

@-webkit-keyframes am-weather-sun {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0)
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg)
  }
}

@keyframes am-weather-sun {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0)
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg)
  }
}

@-webkit-keyframes am-weather-sun-shiny {
  0% {
    stroke-dasharray: 3px 10px;
    stroke-dashoffset: 0
  }

  50% {
    stroke-dasharray: .1px 10px;
    stroke-dashoffset: -1px
  }

  100% {
    stroke-dasharray: 3px 10px;
    stroke-dashoffset: 0
  }
}

@keyframes am-weather-sun-shiny {
  0% {
    stroke-dasharray: 3px 10px;
    stroke-dashoffset: 0
  }

  50% {
    stroke-dasharray: .1px 10px;
    stroke-dashoffset: -1px
  }

  100% {
    stroke-dasharray: 3px 10px;
    stroke-dashoffset: 0
  }
}

@-webkit-keyframes am-weather-moon {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0)
  }

  50% {
    -webkit-transform: rotate(15deg);
    transform: rotate(15deg)
  }

  100% {
    -webkit-transform: rotate(0);
    transform: rotate(0)
  }
}

@keyframes am-weather-moon {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0)
  }

  50% {
    -webkit-transform: rotate(15deg);
    transform: rotate(15deg)
  }

  100% {
    -webkit-transform: rotate(0);
    transform: rotate(0)
  }
}

@-webkit-keyframes am-weather-moon-star-1 {
  0% {
    opacity: 0
  }

  100% {
    opacity: 1
  }
}

@keyframes am-weather-moon-star-1 {
